import React from "react";
import { connect } from 'react-redux';
import { NotificationManager } from 'react-notifications';
import CircularProgress from '@material-ui/core/CircularProgress';
import {
  hideMessage,
  hideAuthLoader,
  showAuthLoader,
  userSignIn,
  userSignInSuccess,
} from '../actions/Auth';
import { switchLanguage } from '../actions/Setting';
import {loginUser} from "../services/service.auth";
import {setUserId , setUserInfo, forceSetToken} from "../services/auth";
import { getUserInfo } from "../services/service.user";
import { cleanAll } from "../services/auth";
import "./styles.css";

class SignIn extends React.Component {
  constructor() {
    super();
    this.state = {
      email: '',
      password: '',
      loading: false,
      width: 0, height: 0
    }
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }
  onLangSwitcherSelect = (event) => {
    this.setState({
      langSwitcher: !this.state.langSwitcher, anchorEl: event.currentTarget
    })
  };

  handleRequestClose = () => {
    this.setState({
      langSwitcher: false,
      userInfo: false,
      mailNotification: false,
      appNotification: false,
      searchBox: false,
      apps: false
    });
  };


  updateWindowDimensions() {
    this.setState({ width: window.innerWidth, height: window.innerHeight });
  }

  componentDidUpdate() {
    if (this.props.authUser !== null) {
      this.props.history.push('/bxt/exchange');
    } else {
      cleanAll();
    }
  }

  handleChange = (event, name, value = false) => {
    this.setState({
      [name]: !value ? event.target.value : event,
    });
  };

  handleSubmit = async (event) => {
    event.preventDefault();
    this.setState({loading:true});
    try{
        const login = await loginUser(this.state.email,this.state.password);
        console.log(login)
        if(login.data.data["/login"].status === 1){
          console.log(login.data.data["/login"].auth_token);
          setUserId(login.data.data["/login"].auth_token);
          forceSetToken(login.data.data["/login"].auth_token);
          try{
            const userInfo = await getUserInfo();
            console.log(userInfo);
            if(userInfo.data.data["/user/info"].status === 1){
              setUserInfo(userInfo.data.data["/user/info"]);
            }
          }catch(error){
            console.log(error);
          }
          this.props.userSignInSuccess(login.data.data["/login"].auth_token)
          this.props.history.push('/exchange');
        }else{
          NotificationManager.error(login.data.data["/login"].message)
          this.setState({loading:false});
        }
    }
    catch(error){
        console.log(error)
    }

  };

  render() {

    // const { showMessage, loader, alertMessage } = this.props;
    return (
      <div  className="container st-container st-effect login-box" style={{marginTop: 0}}>
      <div className="block-page text-center">
        <img src="/images/logo1.png" alt="cryptic-logo"/>
        <h3 className="text-white text-bold">Login to Your Account</h3>
        <div className="row">
          <div className="col-12">
          <form onSubmit={this.handleSubmit} >
              <div className="input--akira">
                <span className="input input--akira">
                  <input
                  className="input__field input__field--akira"
                  type="email"
                  id="input-1"
                  value={this.state.email}
                  autoComplete=""
                  onChange={(event) => this.handleChange(event, "email")}
                  />
                  <label className={`input__label input__label--akira ${this.state.email !== '' ? 'input--filled' : ''}`} htmlFor="input-1">
                    <span className="input__label-content input__label-content--akira">Email</span>
                  </label>
                </span><br/>
                <span className="input input--akira">
                  <input
                  className="input__field input__field--akira"
                  type="password"
                  id="input-2"
                  value={this.state.password}
                  autoComplete="current-password"
                  onChange={(event) => this.handleChange(event, "password")}
                  />
                  <label className={`input__label input__label--akira ${this.state.password !== '' ? 'input--filled' : ''}`} htmlFor="input-2">
                    <span className="input__label-content input__label-content--akira">Password</span>
                  </label>
                </span>
              </div>
              { this.state.loading === true ?
              <span>
              <input className="form-control text-bold" type="button" value={'Loading...'} /><CircularProgress/></span>:
              <button  className="btn btn-cryptic button-element sweetalert31 form-control text-bold" type="submit">Login</button>}
              </form>
          </div>
        </div>
        <p className="text-white" style={{marginTop: "10px"}}><a href="/signup">Create an Account</a></p>
        <p className="text-white"><a href="/recover">Recover your Password</a></p>
      </div>
    </div>
    );
  }
}

const mapStateToProps = ({ auth, settings }) => {
  const { loader, alertMessage, showMessage, authUser } = auth;
  const { locale } = settings;

  return { loader, alertMessage, showMessage, authUser, locale }
};

export default connect(mapStateToProps, {
  userSignIn,
  userSignInSuccess,
  switchLanguage,
  hideMessage,
  hideAuthLoader,
  showAuthLoader,
})(SignIn);
