import axios from "axios";
import { getToken } from "./auth";


const api = axios.create({
  baseURL: process.env.REACT_APP_ENVIROMENT === 'production' ? "//api.nomeexchange.com/api " :
      process.env.REACT_APP_ENVIROMENT === 'development' ? "//env-729395.mircloud.ru/api" :
          process.env.REACT_APP_ENVIROMENT === 'local' ? '//localhost.api.exchange/api' :
              null
});

api.interceptors.response.use(function (response) {


  return response;
}, function (error) {
  console.log("axios",error.response)
  if(error.response.status === 403 || error.response.status === 500){
    localStorage.removeItem('user_id');
    localStorage.removeItem('user_info');
    window.location.href = '/'
  }
  Promise.reject(error);
  return
});

api.interceptors.request.use(async config => {
  if (api.defaults.headers.common['Auth-Token'] !== undefined && api.defaults.headers.common['Auth-Token'] !== null) {
    // console.log('local1')
    return config;
  } else {
    api.defaults.headers.common['Auth-Token'] = await getToken();
    // console.log('local2')
    // console.log(api.defaults.headers.common['Auth-Token'])
    return config;
  }
});

export default api;


