import {
    UPDATE_BOOK,
    STOP_UPDATE_BOOK
} from "../constants/ActionTypes";

const tradeSettings = {
    updadeBook: false,
    tokenName: 'ATK',
};


export default (state = tradeSettings, action) => {
    switch (action.type) {
        case UPDATE_BOOK: {
            return {
                ...state,
                updadeBook: true
            }
        }

        case STOP_UPDATE_BOOK: {
            return {
                ...state,
                updadeBook: false
            }
        }
        default:
            return state;
    }
}
