import React, {Component, Fragment} from 'react';
import {Redirect, Route, Switch} from 'react-router-dom';
import {connect} from 'react-redux';
import {IntlProvider} from 'react-intl'
import AppLocale from '../lngProvider';
import MainApp from '../app/index';
import SignIn from './SignIn';
import SignUp from './SignUp';
import Recover from './Recover';
import RecoverPassword from './RecoverPassword';
import Confirm from './Confirm';
import Terms from './Terms';
import Error404 from '../app/routes/Error404/Error404';
import {setInitUrl} from '../actions/Auth';
import {updateWindowWidth} from '../actions/Setting';
import { NotificationContainer } from 'react-notifications';
// import "../assets/css/notifications.css";
import "./styles.css";
import Exchange from '../app/routes/Exchange/Exchange';
import Landing from '../app/routes/Landing/Landing';

const RestrictedRoute = ({component: Component, authUser, ...rest}) =>
  <Route
    {...rest}
    render={props =>
      authUser
        ? <Component {...props} />
        : <Redirect
          to={{
            pathname: '/signin',
            state: {from: props.location}
          }}
        />

      }
  />;

class App extends Component {

  componentDidMount() {
    window.__MUI_USE_NEXT_TYPOGRAPHY_VARIANTS__ = true;
    if (this.props.initURL === '') {
      this.props.setInitUrl(this.props.history.location.pathname);
    }
    this.props.updateWindowWidth(window.innerWidth);
  }
  componentDidUpdate =() => {
    this.props.updateWindowWidth(window.innerWidth);
  }


  render() {
    
    const {match, location, locale, authUser, initURL} = this.props;

    if (location.pathname === '/') {
      if (authUser === null) {
        return ( <Redirect to={'/signin'}/> );
      } else if (initURL === '' || initURL === '/' || initURL === '/signin' || initURL === '/signup' || initURL === '/terms') {
        return ( <Redirect to={'/signin'}/> );
      } else {
        var conf = initURL.split('/');
       if(conf[1] === 'confirm'){
        return ( <Redirect to={'/bxt/exchange'}/> );
       }else{
        return ( <Redirect to={initURL}/> );
       }
      }
    }

    const currentAppLocale = AppLocale[locale.locale];
    // return (
    //       <IntlProvider
    //         locale={currentAppLocale.locale}
    //         messages={currentAppLocale.messages}>
    //           <div className={`app-main full-height ${location.pathname === '/confirm' || location.pathname === '/signin' || location.pathname === '/signup' || location.pathname === '/recover' || location.pathname === '/recoverpassword' ? 'over': ''}`}>
    //             <Switch>
    //               <RestrictedRoute path={`${match.url}bxt`} authUser={authUser}
    //                                component={MainApp}/>
    //               <Route path='/signin' component={SignIn}/>
    //               <Route path='/signup' component={SignUp}/>
    //               <Route path='/recover' component={Recover}/>
    //               <Route path='/recoverpassword' component={RecoverPassword}/>
    //               <Route path='/confirm' component={Confirm}/>
    //               <Route path='/terms' component={Terms}/>
    //               <Route component={Error404}/>
    //             </Switch>
    //             <NotificationContainer/>
    //           </div>
    //       </IntlProvider>
    // );

    return (
      <Switch>
        <Route path='/exchange' component={Exchange}/>
        <Route path='' component={Landing}/>
      </Switch>
    );
  }
}

const mapStateToProps = ({settings, auth}) => {
  const {sideNavColor, locale, isDirectionRTL} = settings;
  const {authUser, initURL} = auth;
  return {sideNavColor, locale, isDirectionRTL, authUser, initURL}
};

export default connect(mapStateToProps, {setInitUrl, updateWindowWidth})(App);
