import React from "react";
import { connect } from 'react-redux';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import CircularProgress from '@material-ui/core/CircularProgress';
import {
  hideMessage,
  hideAuthLoader,
  showAuthLoader,
  userSignIn
} from '../actions/Auth';
import {setBitcoinWithdrawConfirm} from "../services/service.withdraw";
import { switchLanguage } from '../actions/Setting';
import "./styles.css";


class Confirm extends React.Component {
  constructor() {
    super();
    this.state = {
      email: '',
      password: '',
      userID: '',
      code: '',
      status: 0,
      width: 0, height: 0
    }
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }
  onLangSwitcherSelect = (event) => {
    this.setState({
      langSwitcher: !this.state.langSwitcher, anchorEl: event.currentTarget
    })
  };

  getUrlData = (url) =>{
    const userIDp = url.split('id_user=');
    const userID = userIDp[1].split('&');
    if(userID[0]){
        this.setState({userID: userID[0]})
        const code = userID[1].split('=');
        if(code[1]){
          this.setState({code: code[1]})
        }
    }
    }

  handleRequestClose = () => {
    this.setState({
      langSwitcher: false,
      userInfo: false,
      mailNotification: false,
      appNotification: false,
      searchBox: false,
      apps: false
    });
  };
  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }

  UNSAFE_componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  updateWindowDimensions() {
    this.setState({ width: window.innerWidth, height: window.innerHeight });
  }


  handleChange = (event, name, value = false) => {
    
    this.setState({
      [name]: !value ? event.target.value : event,
    });
  };

  handleWithdrawConfirm = async() =>{
    try{ 
      const confirm = await setBitcoinWithdrawConfirm(this.state.userID,this.state.code);
      console.log(confirm)
      if (confirm.data.data["/withdraw-bitcoin-confirm"].status === 1) {
        NotificationManager.success(`Withdraw Approved!`)
          this.setState({status: 1})
        setTimeout(() => { this.gotohome()}, 3000);
      } else {
        NotificationManager.error(confirm.data.data["/withdraw-bitcoin-confirm"].message)
        this.setState({status: 2})
        setTimeout(() => { this.gotohome()}, 3000);
      }
    }catch(error){
      console.log(error)
    }
  }
  
  gotohome = () =>{
    this.props.history.push('/')
  }

  render() {
    if(this.state.userID === '' && this.state.code === '' && this.state.status === 0){
      this.getUrlData(this.props.location.pathname)
      }
    if(this.state.userID !== '' && this.state.code !== '' && this.state.status === 0){
        this.handleWithdrawConfirm();
      }
    const { showMessage, loader, alertMessage } = this.props;
    return (
      // <div className="container">
         <div  className="container st-container st-effect" style={{marginTop: 0}}>
      <div className="block-page text-center">
        <img src="/images/logo1.png" alt="img-password"/>
        {this.state.status === 0 ? <h3 className="text-white text-bold">Processing</h3> : this.state.status === 1 ?<h3 className="text-white text-bold">Withdraw Approved!</h3> : this.state.status === 2 ?<h3 className="text-white text-bold">Something Went Wrong!</h3>: null}
          <div className="input--akira">
          <div className="loader-view">
            <CircularProgress />
          </div>
          </div>
          <p className="p-4">Redirecting to Home</p>
        <p><a href="/signin">Back to Login</a></p>
        </div>
  
        {
          loader &&
          <div className="loader-view">
            <CircularProgress />
          </div>
        }
        {showMessage && NotificationManager.error(alertMessage)}
        <NotificationContainer />
    </div>
    );
  }
}

const mapStateToProps = ({ auth, settings }) => {
  const { loader, alertMessage, showMessage, authUser } = auth;
  const { locale } = settings;

  return { loader, alertMessage, showMessage, authUser, locale }
};

export default connect(mapStateToProps, {
  userSignIn,
  switchLanguage,
  hideMessage,
  hideAuthLoader,
  showAuthLoader,
})(Confirm);
