import api from "./api";
import { getToken } from "./auth";
const token = getToken();

export const setTokenTransfer = async (amount,address) => {
  var payload = {
    data: { '/user/balance/transfer-token': {'amount':amount,'address':address } }
  };

  if (api.defaults.headers.common['Auth-Token'] === undefined || api.defaults.headers.common['Auth-Token'] === null) {
    var headers = {
      'Accept': 'application/json, text/plain, */*',
      'Auth-Token': token,
    }
    }else{
         headers = {
        'Accept': 'application/json, text/plain, */*',
      }
    }
  return await api.post("", payload, { headers: headers });
}

