export const getWithdrawList= async (idBalance, idStatus, offset = 0, length = 99999) => {
  var payload = {
    data: { '/user/balance/withdraw-list': { 
      'offset': offset,
      'length': length,
      'idBalance':idBalance,
      'idStatus':idStatus,
      'orderBy':'DESC',
    } }
  };
  if (api.defaults.headers.common['Auth-Token'] === undefined || api.defaults.headers.common['Auth-Token'] === null) {
    var headers = {
      'Accept': 'application/json, text/plain, */*',
      'Auth-Token': token,
    }
    }else{
         headers = {
        'Accept': 'application/json, text/plain, */*',
      }
    }
  return await api.post("", payload, { headers: headers });
}