const createUserRequest = async (email, password) =>
  await  api.post("", {data: {'/register': { email: email, password: password }}})
    .then(authUser => authUser)
    .catch(error => error);

const signInUserRequest = async (email , password , token) =>
  await  api.post("", {data: {'/login': { email: email, password: password, token: token }}})
    .then(authUser => authUser)
    .catch(error => error);

const signOutRequest = async () =>
await  api.post("", {data: {'/logout': {}}})
.then(authUser => authUser)
.catch(error => error);

