function* signOut() {
  try {
    const signOutUser = yield call(signOutRequest);
    if (signOutUser.data.data['/logout'].status === 1) {
      localStorage.removeItem('user_id');
      localStorage.removeItem('user_info');
      // localStorage.removeItem('net_info');
      // localStorage.removeItem('hope_info');
      // yield put(userSignOutSuccess(signOutUser));
    } else {
      // yield put(showMessage(signOutUser.message));
    }
  } catch (error) {
    // yield put(showMessage(error));
  }
}

export function* createUserAccount() {
  yield takeEvery(SIGNUP_USER, createUser);
}

export function* signInUserAccount() {
  yield takeEvery(SIGNIN_USER, signInUser);
}

export function* signOutUserAccount() {
  yield takeEvery(SIGNOUT_USER, signOut);
}

export default function* rootSaga() {
  yield all([fork(signInUserAccount),
    fork(createUserAccount),
    fork(signOutUserAccount)]);
}
