import React from "react";
import { connect } from 'react-redux';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import CircularProgress from '@material-ui/core/CircularProgress';
import {
  hideMessage,
  hideAuthLoader,
  showAuthLoader,
  userSignIn
} from '../actions/Auth';
import {setRecoverPasswordConfirm} from "../services/service.user";
import { switchLanguage } from '../actions/Setting';
import "./styles.css";


class RecoverPassword extends React.Component {
  constructor() {
    super();
    this.state = {
      email: '',
      password: '',
      password_confirm:'',
      userID: '',
      code: '',
      width: 0, height: 0
    }
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }
  onLangSwitcherSelect = (event) => {
    this.setState({
      langSwitcher: !this.state.langSwitcher, anchorEl: event.currentTarget
    })
  };

  getUrlData = (url) =>{
    const userIDp = url.split('id_user=');
    const userID = userIDp[1].split('&');
    if(userID[0]){
        this.setState({userID: userID[0]})
        const code = userID[1].split('=');
        if(code[1]){
          this.setState({code: code[1]})
        }
    }
    }

  handleRequestClose = () => {
    this.setState({
      langSwitcher: false,
      userInfo: false,
      mailNotification: false,
      appNotification: false,
      searchBox: false,
      apps: false
    });
  };

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }

  UNSAFE_componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  updateWindowDimensions() {
    this.setState({ width: window.innerWidth, height: window.innerHeight });
  }

  componentDidUpdate() {
    if (this.props.showMessage) {
      setTimeout(() => {
        this.props.hideMessage();
      }, 100);
    }
    if (this.props.authUser !== null) {
      this.props.history.push('/');
    }
  }

  handleChange = (event, name, value = false) => {
    
    this.setState({
      [name]: !value ? event.target.value : event,
    });
  };

  handlePasswordRecover = async() =>{
    try{ 
      const recover = await setRecoverPasswordConfirm(this.state.userID,this.state.code,this.state.password_confirm);
      console.log(recover)
      if (recover.data.data["/recover-password-confirm"].status === 1) {
        NotificationManager.success(`Password Changed!`)
        this.setState({password: '',
        password_confirm:'',
        userID: '',
        code: ''
      });
        this.props.history.push('/signin')
      } else {
        NotificationManager.error(recover.data.data["/recover-password-confirm"].message)
      }
    }catch(error){
      console.log(error)
    }
  }

  render() {
    if(this.state.userID === '' && this.state.code === ''){
      this.getUrlData(this.props.location.pathname)
      }
    const { showMessage, loader, alertMessage } = this.props;
    return (
      // <div className="container">
         <div  className="container st-container st-effect" style={{marginTop: 0}}>
      <div className="block-page text-center">
        <img src="/images/logo1.png" alt="img-password"/>
        <h3 className="text-white text-bold">Recover your Password</h3>
        <p className="text-white text-bold">( UID#{this.state.userID} )</p>
        <form>
          <div className="input--akira">
          <span className="input input--akira">
              <input 
              className="input__field input__field--akira" 
              type="password" 
              id="input-3" 
              value={this.state.password}
              onChange={(event) => this.handleChange(event, "password")}
              autoComplete="new-password"
              />
              <label className={`input__label input__label--akira ${this.state.password !== '' ? 'input--filled' : ''}`} htmlFor="input-3">
                <span className="input__label-content input__label-content--akira">Password</span>
              </label>
            </span>
            <span className="input input--akira">
              <input 
              className="input__field input__field--akira" 
              type="password" 
              id="input-4" 
              value={this.state.password_confirm}
              onChange={(event) => this.handleChange(event, "password_confirm")}
              autoComplete="new-password"
              />
              <label className={`input__label input__label--akira ${this.state.password_confirm !== '' ? 'input--filled' : ''}`}  htmlFor="input-4">
                <span className="input__label-content input__label-content--akira">Repeat Password</span>
              </label>
            </span>
                <br/>
          </div>
          <input className={`form-control text-bold ${this.state.password === '' || this.state.password !== this.state.password_confirm || this.state.code === '' || this.state.userID === '' ? 'button-disabled': ''}`} type="button" value="Change" disabled={this.state.password === '' || this.state.password !== this.state.password_confirm || this.state.code === '' || this.state.userID === '' } onClick={()=>this.handlePasswordRecover()}  />
        </form>
        <p><a href="/signin">Back to Login</a></p>
        </div>
   
        {
          loader &&
          <div className="loader-view">
            <CircularProgress />
          </div>
        }
        {showMessage && NotificationManager.error(alertMessage)}
        <NotificationContainer />
    </div>
    );
  }
}

const mapStateToProps = ({ auth, settings }) => {
  const { loader, alertMessage, showMessage, authUser } = auth;
  const { locale } = settings;

  return { loader, alertMessage, showMessage, authUser, locale }
};

export default connect(mapStateToProps, {
  userSignIn,
  switchLanguage,
  hideMessage,
  hideAuthLoader,
  showAuthLoader,
})(RecoverPassword);
