import React from "react";
import { connect } from 'react-redux';
import {
    hideMessage,
    hideAuthLoader,
    showAuthLoader,
    userSignIn
} from '../../../actions/Auth';
import { switchLanguage } from '../../../actions/Setting';
import ErrorPng from "../../../assets/images/404.png";


class Error404 extends React.Component {
    constructor() {
        super();
        this.state = {
            tradeList: '',
            password: '',
            width: 0, height: 0
        }
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    }
    onLangSwitcherSelect = (event) => {
        this.setState({
            langSwitcher: !this.state.langSwitcher, anchorEl: event.currentTarget
        })
    };

    UNSAFE_componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }

    updateWindowDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
    }

    render() {
        return (
            <div id="dashboard-v1" className="right_col" role="main">
                <div class="block-page text-center">
                    <a href="/"><img src={ErrorPng} alt="404-error" /></a>
                    <h3 className="text-white text-bold">Page Not Found</h3>
                    <p className="second-p text-white">The page you're looking for <br /><span>was not found.</span></p>
                    <div className="go-back">
                        <a className="text-bold" href="/">Go back</a>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = ({ auth, settings, trade }) => {
    const { loader, alertMessage, showMessage, authUser } = auth;
    const { locale } = settings;
    const { tokenName } = trade;

    return { loader, alertMessage, showMessage, authUser, locale, tokenName }
};

export default connect(mapStateToProps, {
    userSignIn,
    switchLanguage,
    hideMessage,
    hideAuthLoader,
    showAuthLoader,
})(Error404);
