import api from "./api";
import { getToken , cleanAll} from "./auth";
const token = getToken();

export const loginUser = async (email,password,atoken) => {

  var payload = {
    data: { '/login': {'email':email,'password':password,'token':atoken} }
  };

  var headers = {
    'Accept': 'application/json, text/plain, */*',
  }
  return await api.post("", payload, { headers: headers });
}

export const logoutUser = async () => {

  var payload = {
    data: { '/logout': {} }
  };
  
  var headers = {
    'Accept': 'application/json, text/plain, */*',
    'Auth-Token': token
  };
  cleanAll();
  return await api.post("", payload, { headers: headers });
}
