import React from "react";
import { connect } from 'react-redux';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import CircularProgress from '@material-ui/core/CircularProgress';
import {
  hideMessage,
  hideAuthLoader,
  showAuthLoader,
  userSignIn
} from '../actions/Auth';
import {setRecoverPassword} from "../services/service.user";
import { switchLanguage } from '../actions/Setting';
import "./styles.css";


class Recover extends React.Component {
  constructor() {
    super();
    this.state = {
      email: '',
      password: '',
      width: 0, height: 0
    }
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }
  onLangSwitcherSelect = (event) => {
    this.setState({
      langSwitcher: !this.state.langSwitcher, anchorEl: event.currentTarget
    })
  };

  handleRequestClose = () => {
    this.setState({
      langSwitcher: false,
      userInfo: false,
      mailNotification: false,
      appNotification: false,
      searchBox: false,
      apps: false
    });
  };
  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }

  UNSAFE_componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  updateWindowDimensions() {
    this.setState({ width: window.innerWidth, height: window.innerHeight });
  }

  componentDidUpdate() {
    if (this.props.showMessage) {
      setTimeout(() => {
        this.props.hideMessage();
      }, 100);
    }
    if (this.props.authUser !== null) {
      this.props.history.push('/');
    }
  }

  handleChange = (event, name, value = false) => {
    
    this.setState({
      [name]: !value ? event.target.value : event,
    });
  };

  handlePasswordRecover = async() =>{
    try{ 
      const recover = await setRecoverPassword(this.state.email);
      console.log(recover)
      if (recover.data.data["/recover-password"].status === 1) {
        NotificationManager.success(`Recover password requested. Check your e-mail for instructions.`)
        this.setState({email:''});
        this.props.history.push('/signin')
      } else {
        NotificationManager.error(recover.data.data["/recover-password"].message)
      }
    }catch(error){
      console.log(error)
    }

  }
  render() {
    const { showMessage, loader, alertMessage } = this.props;
    return (
      // <div className="container">
         <div  className="container st-container st-effect" style={{marginTop: 0}}>
      <div className="block-page text-center">
        <img src="/images/logo1.png" alt="img-password"/>
        <h3 className="text-white text-bold">Recover your Password</h3>
        <form>
          <div className="input--akira">
          <span className="input input--akira">
                  <input 
                  className="input__field input__field--akira" 
                  type="email" 
                  id="input-1" 
                  value={this.state.email}
                  onChange={(event) => this.handleChange(event, "email")}
                  />
                  <label className={`input__label input__label--akira ${this.state.email !== '' ? 'input--filled' : ''}`} htmlFor="input-1">
                    <span className="input__label-content input__label-content--akira">Email</span>
                  </label>
                </span><br/>
          </div>
          <input className={`form-control text-bold ${this.state.email === '' ? 'button-disabled': ''}`} type="button" value="recover now" disabled={this.state.email === ''} onClick={()=>this.handlePasswordRecover()}  />
        </form>
        <p><a href="/signin">Back to Login</a></p>
        </div>
      
        {
          loader &&
          <div className="loader-view">
            <CircularProgress />
          </div>
        }
        {showMessage && NotificationManager.error(alertMessage)}
        <NotificationContainer />
     </div>
    );
  }
}

const mapStateToProps = ({ auth, settings }) => {
  const { loader, alertMessage, showMessage, authUser } = auth;
  const { locale } = settings;

  return { loader, alertMessage, showMessage, authUser, locale }
};

export default connect(mapStateToProps, {
  userSignIn,
  switchLanguage,
  hideMessage,
  hideAuthLoader,
  showAuthLoader,
})(Recover);
