import api from "./api";

export const TOKEN_KEY = "user_id";
export const isAuthenticated = () => localStorage.getItem(TOKEN_KEY) !== null;
export const getToken = () => localStorage.getItem(TOKEN_KEY);
export const getSeedToken = () => JSON.parse(localStorage.getItem("new_user"));
export const login = token => {
  localStorage.setItem(TOKEN_KEY, token.replace(/["]/g, ''));
};
export const setUserId = token => {
  localStorage.setItem(TOKEN_KEY, token.replace(/["]/g, ''));
};

export const setUserInfo = data => {
  localStorage.setItem('user_info', JSON.stringify(data));
};

export const setSeedToken = token => {
  localStorage.setItem(TOKEN_KEY, token.replace(/["]/g, ''));
};

export const logout = () => {
  localStorage.removeItem(TOKEN_KEY);
  localStorage.removeItem('user_info');
};

export const setToken = async () => {
  api.defaults.headers.common['Auth-Token'] = await getToken();
}
export const forceSetToken = (token) => {
  api.defaults.headers.common['Auth-Token'] = token;
}

export const cleanAll = () => {
  localStorage.removeItem('user_id');
  localStorage.removeItem('user_info');
  delete api.defaults.headers.common['Auth-Token'];
}