import api from "./api";
import { getToken } from "./auth";
const token = getToken();


export const registerUser = async (email,password,sponsor) => {
  if(sponsor === ""){ sponsor = undefined;}
  var payload = {
    data: { '/register': {'email':email,'password':password,'sponsor':sponsor } }
  };
  var headers = {
    'Accept': 'application/json, text/plain, */*',
  }
  return await api.post("", payload, { headers: headers });
}

export const getUserInfo = async () => {
  var payload = {
    data: { '/user/info': {} }
  };

  if (api.defaults.headers.common['Auth-Token'] === undefined || api.defaults.headers.common['Auth-Token'] === null) {
  var headers = {
    'Accept': 'application/json, text/plain, */*',
    'Auth-Token': token,
  }
  }else{
       headers = {
      'Accept': 'application/json, text/plain, */*',
    }
  }
  return await api.post("", payload, { headers: headers });
}

